.partnership__wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  padding: 26px 20px;
}

.partnership__lead {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  color: @almost-black;
  margin: 0;
}

.partnership__address {
  font-size: 14px;
  line-height: 20px;
  .paragraph-style-partnership;
  .paragraph-font;
  color: @lightblack;
}
